//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$gotham: 'GothamPro';


@font-face {
  @include fontdefault(normal, 400, $gotham);
  @include fontpath('GothamPro', 'GothamPro', GothamPro); }

@font-face {
  @include fontdefault(normal, 500, $gotham);
  @include fontpath('GothamPro-Medium', 'GothamPro-Medium', GothamPro-Medium); }

@font-face {
  @include fontdefault(normal, 700, $gotham);
  @include fontpath('GothamPro-Bold', 'GothamPro-Bold', GothamPro-Bold); }
