html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 400;
  src: local("GothamPro"), local("GothamPro"), url("../fonts/GothamPro.woff2") format("woff2"), url("../fonts/GothamPro.woff") format("woff");
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 500;
  src: local("GothamPro-Medium"), local("GothamPro-Medium"), url("../fonts/GothamPro-Medium.woff2") format("woff2"), url("../fonts/GothamPro-Medium.woff") format("woff");
}

@font-face {
  font-family: "GothamPro";
  font-style: normal;
  font-weight: 700;
  src: local("GothamPro-Bold"), local("GothamPro-Bold"), url("../fonts/GothamPro-Bold.woff2") format("woff2"), url("../fonts/GothamPro-Bold.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
}

.block {
  display: flex;
}

.wrapper {
  max-width: 1289px;
  min-width: 320px;
  margin: 0 auto;
}

header {
  font-family: 'GothamPro';
}

.logo {
  display: block;
}

.logo__img img {
  display: block;
}

.header-wrapper {
  display: flex;
  width: 100%;
  flex-direction: column-reverse;
}

.menu {
  display: flex;
  justify-content: flex-end;
}

.menu__item a {
  color: #396f8c;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.56px;
  text-decoration: none;
}

.menu__item {
  margin-right: 38px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__item a:hover {
  color: #f58220;
}

.header-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 10px;
}

.lang {
  position: relative;
  color: #396f8c;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.28px;
}

.lang__current {
  border-bottom: 1px dotted #9ab5c6;
  display: block;
  line-height: 12px;
}

.lang-btn {
  display: flex;
  align-items: center;
  align-items: center;
  margin-left: 10px;
  width: 30px;
}

.arrow {
  margin-left: 2px;
}

.rotate {
  transform: rotate(180deg);
}

.arrow {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: #396f8c;
  opacity: 0.7;
  border-bottom: 0;
}

.arrow:hover {
  border-top-color: #f58220;
}

.lang-list {
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  flex-direction: column;
  align-items: flex-start;
  background-color: #eaeff2;
}

.lang-list__item {
  padding: 10px;
  font-size: 14px;
}

.lang__link {
  text-decoration: none;
  color: #396f8c;
}

.lang__link:hover {
  color: #f58220;
}

.sign-in-btn {
  opacity: 0.8;
  color: #396f8c;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.7px;
}

.sign-in-btn:hover {
  color: #2e5b72;
  opacity: 1;
}

.header-right > div {
  margin-right: 40px;
}

.header-right > div:last-child {
  margin-right: 0;
}

.find-block {
  display: flex;
  position: relative;
}

.find-block__input {
  width: 250px;
  padding: 7px 27px 7px 4px;
  background-color: #cadae3;
}

.find-arrow {
  background-image: url(/img/find.png);
  background-repeat: no-repeat;
  display: block;
}

.sprite-find {
  width: 17px;
  height: 17px;
  background-position: -5px -5px;
  position: absolute;
  width: 17px;
  height: 17px;
  right: 7px;
  top: 50%;
  transform: translateY(-50%);
}

.sprite-find:hover {
  background-position: -32px -5px;
}

.yura {
  display: none;
}
